@use "../colors" as *;

html,
body {
  overflow: hidden;
  // transition: background-color 350ms ease;
}

h2 {
  font-size: 2rem;
  margin: 25px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  text-transform: uppercase;
  @include themed {
    color: t("text");
  }
  transition: color 350ms ease;
}

h3 {
  font-size: 1.5rem;
  margin: 15px;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  color: hsl(0, 0%, 10%);
}

p {
  font-size: 1.2rem;
  font-family: "Montserrat", sans-serif;
  color: hsl(0, 0%, 10%);
}