@use "../../../styles/colors" as *;

.sizes {
  display: flex;
  width: 100%;
  margin-top: auto;
}

.sizes-numbers {
  display: flex;
  width: 100%;
  overflow-x: scroll;
}

.numbers {
  padding-left: 10px;
  padding-right: 10px;
}

.numberSelected {
  border-radius: 15px;
  background-color: $grey-500-300;
}